import React, { Component } from 'react';
import ContactService from '../services/ContactService';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faArrowCircleLeft, faRecycle } from '@fortawesome/free-solid-svg-icons';
import TransferFund from '../components/TransferFund'
import MoveList from '../components/MoveList'
import UtilService from '../services/utilService'



export default class ContactDetailsPage extends Component {
    state = {
        user: {},
    }
    async componentDidMount() {

        var user = await ContactService.getContactById(this.props.match.params.id)
        this.setState({
            user
        })
    }

    render() {
        if (!sessionStorage.loggedInUser) this.props.history.push('/');
        var loggedInUser = UtilService.loadFromSession('loggedInUser')
        var { name, email, phone, _id } = this.state.user;

        var moves = loggedInUser.moves.filter((move) => {
            return move.toId === _id
        }).sort((a, b) => { return b.at - a.at })
        console.log(moves.length)
        var elBtn = (moves.length > 3)? (<Link className="btn-all-history" to="/moves">
            All History â†’
        </Link>) : null;

        moves = moves.slice(0, 3)
        return (
            <section className="contact-details-page">
                <div className="contact-details-hero contact-details-hero-mobile flex column center">
                    <div className="contact-details-content container flex column center space-between">
                        <div className="info">
                            <h2 className="flex row center space-between">
                                <span className="icon" onClick={this.props.history.goBack}><FontAwesomeIcon icon={faArrowCircleLeft}></FontAwesomeIcon></span>
                                <span className="main-title">Contact Details</span>
                                <Link className="icon" to={'/contact/edit/' + _id}><FontAwesomeIcon icon={faEdit}></FontAwesomeIcon></Link>
                            </h2>
                            <div className="main-details-container flex row center space-between">
                                <div className="main-details-wrapper">
                                    <div className="flex row center space-between info-first-header">
                                        <div className="img-container flex row center justify-start" >
                                            <img className="avatar" alt="Avatar" src="data:image/jpeg;base64,"></img>
                                            <div>{name}</div>
                                        </div>
                                    </div>
                                    <div>Email : {email}</div>
                                    <div>Phone No. : {phone}</div>
                                    <TransferFund toId={_id} userCoins={loggedInUser.coins} />
                                </div>
                                <div className="move-history-container flex column center align-end">
                                    <span className="move-history-title"><FontAwesomeIcon icon={faRecycle} /> Moves Hisotry</span>
                                    <MoveList moves={moves} />
                                    {elBtn}
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </section>
        )
    }
}