import React, { Component } from 'react';
import ContactService from '../services/ContactService';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowCircleRight } from '@fortawesome/free-solid-svg-icons';



export default class ContactEditPage extends Component {
    state = {
        contact: {
            _id: '',
            name: '',
            email: '',
            phone: ''
        }
    }

    async componentDidMount() {
        const { id } = await this.props.match.params
        if (id) {
            const contact = await ContactService.getContactById(id)
            if (contact) this.setState({ contact })
        }
    }

    updateContact = (ev) => {
        const { value, id } = ev.target
        this.setState((prevState) => {
            return {
                contact: {
                    ...prevState.contact,
                    [id]: value
                }
            }
        })
    }

    saveContact = async () => {
        const { name, phone, email } = this.state.contact;
        if (name && phone && email) {
            await ContactService.saveContact(this.state.contact)
            console.log('saved!')
            this.props.history.push('/contact')
        }
    }

    removeContact = async () => {
        await ContactService.deleteContact(this.props.match.params.id);
        console.log('Delete!')
        this.props.history.push('/contact')
    }

    render() {
        //Avoid Uncontrolled undefiend
        const { contact } = this.state
        const elBtn = (this.props.match.params.id) ?
            (<button className="btn-details btn-delete" onClick={this.removeContact}>Delete Contact</button>) :
            null
        return (
            <section className="contact-edit-page">
                <div className="contact-details-hero contact-details-hero-mobile flex column center">
                    <div className="contact-edit-content container flex column center space-between">
                        <div className="info">
                            <h2 className="edit-title flex row center space-between">
                                <span className="main-title">Contact Edit</span>
                                <FontAwesomeIcon onClick={this.props.history.goBack} className="icon" icon={faArrowCircleRight}></FontAwesomeIcon>
                            </h2>
                            <div className="img-container info-wrapper flex row center justify-start">
                                <img className="avatar" alt="Avatar" src="data:image/jpeg;base64,"></img>
                                <input id="name"
                                    onChange={(ev) => this.updateContact(ev)}
                                    type="text"
                                    placeholder="Enter Change"
                                    value={contact.name} />
                            </div>
                            <div className=" info-wrapper flex row center"> Email : <input id="email"
                                onChange={(ev) => this.updateContact(ev)}
                                type="text"
                                placeholder="Enter Email"
                                value={contact.email} /></div>
                            <div className="info-wrapper flex row center">
                                Phone No. : <input id="phone"
                                    onChange={(ev) => this.updateContact(ev)}
                                    type="text"
                                    placeholder="Enter Phone"
                                    value={contact.phone} /></div>
                        </div>
                        <div className="edit-btns-container flex row center">
                            <button className="btn-details btn-save" onClick={this.saveContact}>
                                Save Contact
                        </button>
                            {elBtn}
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}



